/*
  When navigating to an anchor point, scroll smoothly
*/
main {
    scroll-behavior: smooth;
}

nav {
    --y-padding: 24px;

    display: flex;
    flex-direction: row;
    width: 100vw;
    padding: var(--y-padding) 24px var(--y-padding) 0px;
    align-items: center;

    /*
      Without z-index > 2 the mobile dropdown menu fails to cover the <mark>
      tags
    */
    z-index: 3;


    /*
      On mobile, we want the #main-nav-container -- a child of this element, to
      be obscured by the nav menu. It is not possible to hide a child behind
      it's parent using z-index, but it is possible with
      `transform-style: preserve-3d` and `transform: translateZ(..)` on the
      child:
        https://fjolt.com/article/css-make-child-appear-behind-parent
    */
    transform-style: preserve-3d;
}

nav a {
    font-size: 12px;
}

#anmal {
    margin-right: 28px;
    color: black; /* TODO Use dark-gray color from design */
    background-color: white;
    border-radius: 24px;
    padding: 10px 24px;
    text-decoration: none;
    box-shadow: #615e5e80 2px 2px 2px;
}

#main-nav-container {
    display: flex;
    flex-direction: row;

    /*
      gap: 24px; gap not supported in all targeted browsers, we simulate it
      usin margin-right on the children (see below).
    */
}

#main-nav-container > * {
    margin-right: 24px;
}
#main-nav-container > *:last-child {
    margin-right: 0px;
}

#hamburger {
    display: none;
}

#main-nav-outer-container {
    order: 3;
}

#nav-overlay {
    display: none;
}

@media screen and (max-width: 700px) and (orientation: portrait) {
    nav a {
        font-size: 16px;
    }

    /*
      The hamburger icon.

      The hamburger icons has the following CSS structure
        <button>
            <div></div>
            <div></div>
            <div></div>
        </button>

      where each <div> is styled individually using :nth-child(n) to achive the
      three bars. Then the bars are rotated / changed when the hamburger icon
      has the "show" class. This together with CSS transition creates a nice
      animation.
    */

    #hamburger {
        /* How high should an individual bar be */
        --bar-height: 2px;

        display: initial;

        position: relative;
        margin-right: 0px;
        width: 28px;
        height: 20px;
        border: none;
        background: none;
        cursor: pointer;

    }

    #hamburger > * {
        display: block;
        position: absolute;
        height: var(--bar-height);
        width: 100%;
        background: white;
        opacity: 1;
        left: 0;
        transform: rotate(0deg);
        transition: transform .25s, top .25s, width 0.25s, opacity .25s;
    }

    #hamburger > *:nth-child(1) {
        top: 0px;
    }

    #hamburger > *:nth-child(2) {
        top: calc(50% - var(--bar-height) / 2);
        width: 100%;
    }

    #hamburger > *:nth-child(3) {
        top: calc(100% - var(--bar-height));
    }

    #hamburger.show > *:nth-child(1) {
        transform: rotate(45deg);
        top: calc(50% - var(--bar-height) / 2);
    }

    #hamburger.show > *:nth-child(2) {
        width: 0%;
        opacity: 0;
    }

    #hamburger.show > *:nth-child(3) {
        transform: rotate(-45deg);
        top: calc(50% - var(--bar-height) / 2);
    }

    #main-nav-outer-container {
        order: initial;
        position: relative;
        width: 0px;
        height: 0px;
        align-self: flex-end;

        /* Have the same bg-color as the <nav> tag (which is set dynamically) */
        background-color: inherit;

        /*
          Position this container "behind" it's parent.
          See comment over `transform-style: preserve-3d;` for more info.
        */
        transform: translateZ(-100px);
    }

    #main-nav-container {
        /*
          Position the dropdown just out of sight.
        */
        position: relative;
        left: 0px;
        bottom: calc(0px - var(--y-padding));
        width: 100vw;
        transform: translateY(-100%);

        /* Have the same bg-color as the <nav> tag (which is set dynamically) */
        background-color: inherit;

        color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-bottom: 8px;
        box-shadow: #00000000 0px 4px 4x;

        transition: box-shadow .5s, transform .5s;
    }

    #main-nav-container.show {
        transform: translateY(0%);
        box-shadow: #00000045 0px 4px 4px;
    }

    #main-nav-container > * {
        margin-right: 0px;
        margin-bottom: 12px;
    }

    /*
      This is the thing that makes the background go dark when the dropwdown
      opens.
    */
    #nav-overlay {
        display: initial;
        position: fixed;
        top: 0px;
        bottom: 0px;
        width: 0px;
        height: 0px;
        background-color: #0009;
        z-index: 2;
        opacity: 0;
        transition: opacity .5s;
    }

    #nav-overlay.show {
        width: 100vw;
        height: 100vh;
        opacity: 1;
    }
}
